<template>
  <div>
  <div id="logo_wrapper">
    <svg class="logo" xmlns="http://www.w3.org/2000/svg" xmlns:svg="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000">
      <g transform="scale(0.5) translate(500,500)" id="logo">
        <circle id="logo_circle" fill="#010202" class="st0" cx="500" cy="500" r="500"></circle>
        <path id="logo_label" fill="#fff" class="st1" d="M143.1 555.6h-23V434.8h23v120.8zM228.8 555.6H206V434.8l80.8 71.2v-71.2h22.8v120.8l-80.8-67.3v67.3zM420.1 508.90000000000003l36-74.1h25.1l-61 120.8-61-120.8h25.1l35.8 74.1zM529.3 555.6V434.8h23v120.8h-23zM836 434.8l58.8 120.7h-23.9l-8.6-18.9h-52.4l-8.6 18.9-24 .1L836 434.8zm-.1 43.8l-17.5 38.6h35.2l-17.7-38.6zM722.1 586.6h-25.7l-15.1-26.3c-2.5.3-5.1.5-7.8.5-34.7 0-62.9-28.2-62.9-62.9s28.2-62.9 62.9-62.9 62.9 28.2 62.9 62.9c0 24.5-14 45.7-34.5 56.1l20.2 32.6zm-48.6-129c-22.2 0-40.2 18.1-40.2 40.2 0 22.2 18.1 40.2 40.2 40.2 22.2 0 40.2-18.1 40.2-40.2s-18-40.2-40.2-40.2z"></path>
      </g>
    </svg>
  </div>
  <div id="trigger"></div>
</div>
</template>

<script>
import ScrollMagic from 'ScrollMagic';
import gsap from 'gsap/TweenMax';

require('animation.gsap')
require('debug.addIndicators')

gsap.CSSPlugin.defaultSmoothOrigin = true;

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted() {
    const
      trigger = document.getElementById('trigger'),
      wrapper = document.getElementById('logo_wrapper'),
      logo = document.getElementById('logo'),
      circle = document.getElementById('logo_circle'),
      label = document.getElementById('logo_label'),
      tl = new gsap.TimelineMax();

      console.log(
      'w',wrapper,
      'l',logo,
      'c',circle,
      'la',label)

      tl
      .fromTo(wrapper, 3,
        {backgroundColor: "#fff"},
        {backgroundColor: "#000", ease: gsap.Power2.easeOutIn}
      )
      .fromTo(label, 3,
        {fill: "#000", rotation:0, scale: 0, transformOrigin:"50% 50%"},
        {fill: "#fff", rotation:360, scale: 1, transformOrigin:"50% 50%", ease: gsap.Power2.easeInOut},
        '-=3'
      )
      // .fromTo(circle, 0.4,
      //   {fill: "transparent", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
      //   {fill: "#000", scale: 1, autoAlpha: 1, transformOrigin: "50% 50%", repeat: 1, yoyo: true, ease: gsap.Power2.easeInOut}, '-=1'
      // )
      .fromTo(circle, 2,
        {fill: "transparent", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
        {fill: "#19a69c", scale: 2, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power4.easeIn}
      )
      // .set(wrapper, {backgroundColor: "#19a69c"})
      .to(wrapper, 0.5, {backgroundColor: "#19a69c", ease: gsap.Power4.easeOut})
      .fromTo(circle, 2,
        {fill: "transparent", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
        {fill: "#EA168C", scale: 2, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power4.easeIn}
      )
      // .set(wrapper, {backgroundColor: "#EA168C"})
      .to(wrapper, 0.5, {backgroundColor: "#EA168C", ease: gsap.Power4.easeOut})
      .fromTo(circle, 2,
        {fill: "transparent", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
        {fill: "#FBB831", scale: 2, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power4.easeIn}
      )
      // .set(wrapper, {backgroundColor: "#FBB831"})
      .to(wrapper, 0.5, {backgroundColor: "#FBB831", ease: gsap.Power4.easeOut})
      .fromTo(circle, 2,
        {fill: "transparent", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
        {fill: "#77308D", scale: 2, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power4.easeIn}
      )
      // .set(wrapper, {backgroundColor: "#77308D"})
      .to(wrapper, 0.5, {backgroundColor: "#77308D", ease: gsap.Power4.easeOut})
      .fromTo(circle, 3,
        {fill: "#000", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
        {fill: "#000", scale: 1, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power2.easeInOut}
      )
      .to(wrapper, 3,
        {backgroundColor: "#fff", ease: gsap.Power2.easeInOut},
        '-=3'
      )
      .to(label, 2, {rotation:0, transformOrigin: "top center", ease: gsap.Power1.easeInOut})
      .to(logo, 2, {scale: 0.2, y: 0, transformOrigin: "bottom center", ease: gsap.Power1.easeOutIn}, '-=2')
      .to(logo, 2, {y: 500, transformOrigin: "bottom center", ease: gsap.Bounce.easeOut}, '+=1')

      tl.play();
      
      // const sm = new ScrollMagic.Controller();
      // sm.addScene(new ScrollMagic.Scene({
      //   triggerElement: trigger,
      //   duration: trigger.offsetHeight
      // }).setTween(tl));
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #trigger {
    height: 5000px;
  }
  #logo_wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 60px;
    background: #fff;
  }
  .logo{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
</style>



